div.openDeck-view-hardwareDashboard-Canvas>svg {
    margin: 0;
    position: absolute;
    width: 100%;
    height: 100%;
}

div.openDeck-view-hardwareDashboard-Canvas>svg * {
    transition: opacity 1s;
}

div.openDeck-view-hardwareDashboard-Canvas>svg {
    width: 100%;
}

@-webkit-keyframes opendeckspin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(-360deg);
    }
}

@keyframes opendeckspin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(-360deg);
    }
}

.openDeck-view-hardwareDashboard-Canvas>svg use.spin {
    -webkit-animation-name: opendeckspin;
            animation-name: opendeckspin;
    -webkit-animation-duration: .7s;
            animation-duration: .7s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

