div.view-Errors {
    width: 20vw;
    flex: 0 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-direction: column;
}

div.view-Errors > div.body {
    flex: 1 1 auto;
    overflow: auto;
    margin: .25rem;
    padding: .5rem;
}

div.view-Errors > div.body > div {
    color: #f00;
    font-weight: bold;
    font-family: monospace;
}

div.view-ProjectorState {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0 8px;
    align-items: stretch;
    width: 120px;
    height: 9rem;
}

div.view-ProjectorState > label {
    opacity: .5;
    text-align: center;
}

html, body {
    font-family: sans-serif;
    font-size: 14px;
}

div.App {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
}

div.App > div:first-child {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 280px;
}

div.App > div:last-child {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 280px;
}

div.App div.projectors {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    width: 100%;
    height: 280px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
}


div.App div.openDeck-view-hardwareDashboard-Canvas {
    position: absolute;
    left: 0;
    top: 200px;
    bottom: 0;
    right: 0;
    width: 100%;
}

